.page {
  box-sizing: border-box;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  margin: 0;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  opacity: 1;
  z-index: 100;
  pointer-events: all;
  transform: translateX(0);
}

.page__header {
  background-color: #2c2c2c;
  border-radius: 0 0 30px 30px;
}

